    --border-radius: 5px;
    --background-light: #f7f7f7;
    --background-medium: #e7e7e7;
    --background-dark: #d4d4d4;
    --background-active: #4CAF50;
    --background-button: #007BFF;
    --background-button-hover: #0056b3;
    --border-color: #ccc;
    --transition-speed: 0.3s ease;
    --icon-bg-color: #4CAF50;
    --icon-bg-color-row2: #FF0000;
    --text-bg-color: #007BFF;
    --right-icon-bg-color: #007BFF;
    --icon-eye-open: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    --icon-eye-closed: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
    --icon-file: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
    --icon-folder: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
    
    --size-padding-small: 5px;
    --size-padding-medium: 10px;
    --size-padding-large: 15px;
    --size-margin-small: 5px;
    --size-margin-medium: 10px;
    --size-border-radius: 5px;
    --size-element-height: 40px;
    --size-font-default: 14px;
    --size-font-large: 16px;
    --width-input: 80px;
    --width-filetype-input: 200px;
    
    --color-text: #333;
    --color-text-light: #fff;
    --color-text-muted: #666;
    --color-border: #ccc;
    --color-background-light: #f7f7f7;
    --color-background-medium: #e7e7e7;
    --color-background-dark: #d4d4d4;
    --color-background-active: #4CAF50;
    --color-button: #007BFF;
    --color-button-hover: #0056b3;
}

*, *::before, *::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html, body {
    height: 100%;
    width: 100%;
    overflow-x: hidden;
}

.main-container {
    padding: var(--size-padding-small) var(--size-padding-medium);
    display: flex;
    flex-direction: column;
    align-items: stretch;
    width: calc(100vw - 2 * var(--size-padding-medium));
    max-width: 100%;
}

.textarea {
    width: 100%;
    padding: var(--size-padding-medium);
    border: 1px solid var(--color-border);
    border-radius: var(--size-border-radius);
    margin: var(--size-margin-small) 0;
    background-color: var(--color-background-light);
    resize: vertical;
    min-height: 30px;
    overflow: auto;
}

.button {
    height: var(--size-element-height);
    padding: 0 var(--size-padding-large);
    border: none;
    border-radius: var(--size-border-radius);
    cursor: pointer;
    transition: background-color var(--transition-speed);
    line-height: var(--size-element-height);
    white-space: nowrap;
    font-size: var(--size-font-default);
}

#compression-buttons {
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
}

#compression-buttons .label {
    margin-right: var(--size-margin-small);
}

.compression-button {
    height: var(--size-element-height);
    margin-right: var(--size-margin-medium);
    padding: 0 var(--size-padding-large);
    border-radius: var(--size-border-radius);
    background-color: var(--color-background-medium);
    color: var(--color-text);
    border: none;
    cursor: pointer;
    transition: background-color var(--transition-speed);
    line-height: var(--size-element-height);
    font-size: var(--size-font-default);
}

.compression-button:hover {
    background-color: var(--color-background-dark);
}

.compression-button.selected {
    background-color: var(--color-background-active);
    color: var(--color-text-light);
}

.webpage-button {
    background-color: var(--color-button);
    color: var(--color-text-light);
}

.webpage-button:hover {
    background-color: var(--color-button-hover);
}

.horizontal-section {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap;
    margin-top: var(--size-margin-medium);
}

.counter {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0 var(--size-margin-small);
    flex-basis: auto;
}

.counter .label {
    margin-right: var(--size-margin-small);
}

.input {
    width: var(--width-input);
    height: var(--size-element-height);
    padding: 0 var(--size-padding-large);
    border: none;
    border-radius: var(--size-border-radius);
    background-color: var(--color-background-medium);
    text-align: center;
    cursor: not-allowed;
    color: var(--color-text);
    transition: background-color var(--transition-speed);
    margin: 0 var(--size-margin-small);
    line-height: var(--size-element-height);
    font-size: var(--size-font-default);
}

.input:hover {
    background-color: var(--color-background-dark);
}

.json-types-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: var(--size-margin-medium);
}

.instructions {
    margin-bottom: var(--size-margin-medium);
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    text-align: center;
}

.instructions p {
    font-size: var(--size-font-large);
    color: var(--color-text-muted);
    margin: 0;
}

.row {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: var(--size-margin-medium) 0;
    width: 100%;
    min-height: calc(var(--size-element-height) + 2 * var(--size-margin-small));
}

.box {
    display: flex;
    align-items: stretch;
    height: 50px;
    margin: 5px;
    cursor: grab;
    user-select: none;
    border-radius: var(--border-radius);
    transition: background-color var(--transition-speed), transform 0.5s ease, opacity var(--transition-speed);
    overflow: hidden;
}

.box:active {
    cursor: grabbing;
}

.box.hidden {
    opacity: 0.5;
}

.box .icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 100%;
    background-color: var(--icon-bg-color);
    cursor: grab;
    flex-shrink: 0;
}

#row2 .box .icon {
    background-color: var(--icon-bg-color-row2);
}

#row2 .box .right-icon {
    background-color: var(--right-icon-bg-color);
}

.box .left-icon {
    border-top-left-radius: var(--border-radius);
    border-bottom-left-radius: var(--border-radius);
}

.box .left-icon::before {
    content: '';
    display: inline-block;
    background: var(--icon-file) no-repeat center center / contain;
    width: 24px;
    height: 24px;
}

.box .text {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--text-bg-color);
    cursor: grab;
    height: 100%;
    padding-left: 1em;
    padding-right: 1em;
    color: var(--color-text-light);
}

.box .right-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 100%;
    background-color: var(--right-icon-bg-color);
    border-top-right-radius: var(--border-radius);
    border-bottom-right-radius: var(--border-radius);
    flex-shrink: 0;
}

#row1 .box .text {
    border-top-right-radius: var(--border-radius);
    border-bottom-right-radius: var(--border-radius);
}

#row2 .box .text {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}

.box .right-icon::before {
    content: '';
    display: inline-block;
    background: var(--icon-eye-open) no-repeat center center / contain;
    width: 24px;
    height: 24px;
}

.box .right-icon.open-eye::before {
    background-image: var(--icon-eye-open);
}

.box .right-icon.closed-eye::before {
    background-image: var(--icon-eye-closed);
}

.line {
    width: 100%;
    height: 2px;
    background-color: var(--color-border);
    margin: var(--size-margin-medium) 0;
}

.placeholder {
    height: var(--size-element-height);
    background-color: transparent;
    border: 2px dotted var(--color-border);
    margin: var(--size-margin-small);
    border-radius: var(--size-border-radius);
    padding: 0 var(--size-padding-medium);
}

.icon {
    display: inline-flex;
    align-items: center;
    margin-right: 0.5em;
    width: 1.2em;
    height: 1.2em;
}

.icon-file::before {
    content: '';
    display: inline-block;
    background: var(--icon-file) no-repeat center center / contain;
    width: 24px;
    height: 24px;
}

.icon-folder::before {
    content: '';
    display: inline-block;
    background: var(--icon-folder) no-repeat center center / contain;
    width: 24px;
    height: 24px;
}

.size-6 {
    width: 1.2em;
    height: 1.2em;
}

.filetype-input {
    width: var(--width-filetype-input);
    height: var(--size-element-height);
    padding: 0 var(--size-padding-medium);
    margin: var(--size-margin-medium) 0;
    border: 1px solid var(--color-border);
    border-radius: var(--size-border-radius);
    font-size: var(--size-font-default);
    display: block;
    margin-left: auto;
    margin-right: auto;
    cursor: text;
    line-height: var(--size-element-height);
}

/* Remove any margin or padding that might be causing gaps */
.box > * {
    margin: 0;
    padding: 0;
}

@media (max-width: 890px) {
    .horizontal-section {
        flex-direction: column;
        align-items: stretch;
    }

    .horizontal-section > * {
        margin-bottom: var(--size-margin-medium);
    }

    .json-types-container, .json-types-list {
        padding: 0 var(--size-padding-medium);
    }

    .main-container {
        display: flex;
        flex-direction: column;
        height: 100%;
    }

    .button, .input, .compression-button {
        width: 100%;
    }

    .filetype-input {
        width: calc(100% - 2 * var(--size-padding-medium));
    }
}

@media (max-width: 500px) {
    .button, .input, .compression-button, .textarea {
        padding: 0 var(--size-padding-medium);
    }

    .filetype-input {
        font-size: var(--size-font-default);
